export default {
	data() {
		return {
			previousNo:'',
			hide: true,
			aitem: {
				type: Array,
				default () {
					return []
				}
			},
		}
	},
	methods: {
		toggle() {
			var that = this;
			that.hide = !that.hide;
		}
	},
	onLoad() {
		var that = this;
		uni.request({
			url: 'https://h5.shangchaocai.cn/open/getRecentLottery',
			method: 'POST',
			data: {
				"type": 3,
				"num": 10
			},
			success: res => {
				that.aitem = res.data.list;
			},
			fail: () => {},
			complete: () => {}
		});
		uni.request({
			url: 'https://h5.shangchaocai.cn/h5/getCurrentTerm',
			method: 'POST',
			data: {
				type:3
			},
			success: res => {
				var current = res.data.term_no;
				uni.request({
					url: 'https://h5.shangchaocai.cn/h5/getLastTerm',
					method: 'POST',
					data: {
						type:3,
						term_no: current-1
					},
					success: res => {
						that.previousNo = res.data.result.split(";").join(" ");
					},
					fail: () => {},
					complete: () => {}
				});
			}
		});
	}
}
